export default {
	data() {
		return {
			afterHeaderOpacity: 1, //不透明度
			headerPosition: 'fixed',
			headerTop: null,
			statusTop: null,
			city: '',
			currentSwiper: 0,
			// 轮播图片
			swiperList: [{
					id: 1,
					src: 'url1',
					img: '../../static/images/ncmq9n5pmdli1tlb0h8e.jpg'
				},
				{
					id: 2,
					src: 'url2',
					img: '../../static/images/ojmotpyelp9tivef4k6d.jpg'
				}

			],
			// 分类菜单
			categoryList: [
				{
					id: 1,
					name: '书籍1',
					img: '../../static/img/category/7.png'
				}
			],
			Promotion: [],
			//猜你喜欢列表
			productList: [{
					goods_id: 0,
					img: '../../static/img/goods/p1.jpg',
					name: '商品名称商品名称商品名称商品名称商品名称',
					price: '￥168',
					slogan: '1235人付款'
				}
			],
			loadingText: '正在加载...'
		};
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop >= 0 ? "fixed" : "absolute";
		this.headerTop = e.scrollTop >= 0 ? null : 0;
		this.statusTop = e.scrollTop >= 0 ? null : -this.statusHeight + 'px';
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
	onReachBottom() {
		uni.showToast({
			title: '触发上拉加载'
		});
		let len = this.productList.length;
		if (len >= 40) {
			this.loadingText = '到底了';
			return false;
		}
		// 演示,随机加入商品,生成环境请替换为ajax请求
		let end_goods_id = this.productList[len - 1].goods_id;
		for (let i = 1; i <= 10; i++) {
			let goods_id = end_goods_id + i;
			let p = {
				goods_id: goods_id,
				img: '../../static/img/goods/p' + (goods_id % 10 == 0 ? 10 : goods_id % 10) + '.jpg',
				name: '商品名称商品名称商品名称商品名称商品名称',
				price: '￥168',
				slogan: '1235人付款'
			};
			this.productList.push(p);
		}
	},
	getIndexData() {
		let that = this;
		util.request(api.IndexUrl).then(function(res) {
			if (res.errno === 0) {
				that.setData({
					newGoods: res.data.newGoodsList,
					hotGoods: res.data.hotGoodsList,
					topics: res.data.topicList,
					brands: res.data.brandList,
					floorGoods: res.data.floorGoodsList,
					banner: res.data.banner,
					groupons: res.data.grouponList,
					channel: res.data.channel,
					coupon: res.data.couponList
				});
			}
		});
	},
	onLoad(options) {
		//开启定时器
		//this.Timer();
		//加载活动专区
		//this.loadPromotion();
		this.getIndexData();
		if (options.scene) {
			//这个scene的值存在则证明首页的开启来源于朋友圈分享的图,同时可以通过获取到的goodId的值跳转导航到对应的详情页
			var scene = decodeURIComponent(options.scene);
			console.log("scene:" + scene);

			let info_arr = [];
			info_arr = scene.split(',');
			let _type = info_arr[0];
			let id = info_arr[1];

			if (_type == 'goods') {
				wx.navigateTo({
					url: '../goods/goods?id=' + id
				});
			} else if (_type == 'groupon') {
				wx.navigateTo({
					url: '../goods/goods?grouponId=' + id
				});
			} else {
				wx.navigateTo({
					url: '../index/index'
				});
			}
		}
		// 页面初始化 options为页面跳转所带来的参数
		if (options.grouponId) {
			//这个pageId的值存在则证明首页的开启来源于用户点击来首页,同时可以通过获取到的pageId的值跳转导航到对应的详情页
			wx.navigateTo({
				url: '../goods/goods?grouponId=' + options.grouponId
			});
		}
		// 页面初始化 options为页面跳转所带来的参数
		if (options.goodId) {
			//这个goodId的值存在则证明首页的开启来源于分享,同时可以通过获取到的goodId的值跳转导航到对应的详情页
			wx.navigateTo({
				url: '../goods/goods?id=' + options.goodId
			});
		}

		// 页面初始化 options为页面跳转所带来的参数
		if (options.orderId) {
			//这个orderId的值存在则证明首页的开启来源于订单模版通知,同时可以通过获取到的pageId的值跳转导航到对应的详情页
			wx.navigateTo({
				url: '../ucenter/orderDetail/orderDetail?id=' + options.orderId
			});
		}

		this.getIndexData();



	},

	methods: {
		//加载Promotion 并设定倒计时,,实际应用中应该是ajax加载此数据。
		loadPromotion() {
			let cutTime = new Date();
			let yy = cutTime.getFullYear(),
				mm = cutTime.getMonth() + 1,
				dd = cutTime.getDate();
			let tmpcountdown = yy + '/' + mm + '/' + dd + ' 23:59:59';
			let tmpPromotion = [{
					title: '整点秒杀',
					ad: '整天秒杀专区',
					img: '../../static/img/s1.jpg',
					countdown: false
				},
				{
					title: '限时抢购',
					ad: '每天23点上线',
					img: '../../static/img/s2.jpg',
					countdown: tmpcountdown
				} //countdown为目标时间，程序会获取当前时间倒数
			];
			//检查倒计时
			for (let i = 0; i < tmpPromotion.length; i++) {
				let row = tmpPromotion[i];
				if (row.countdown) {
					let h = '00',
						m = '00',
						s = '00';
					let currentTime = new Date();
					let cutoffTime = new Date(tmpcountdown);
					if (!(currentTime >= cutoffTime)) {
						let countTime = parseInt(
							(cutoffTime.getTime() - currentTime.getTime()) / 1000
						);
						h = parseInt(countTime / 3600);
						m = parseInt((countTime % 3600) / 60);
						s = countTime % 60;
						h = h < 10 ? '0' + h : h;
						m = m < 10 ? '0' + m : m;
						s = s < 10 ? '0' + s : s;
					}
					tmpPromotion[i].countdown = {
						h: h,
						m: m,
						s: s
					};
				}
			}
			this.Promotion = tmpPromotion;
		},
		//定时器
		Timer() {
			setInterval(() => {
				if (this.Promotion.length > 0) {
					for (let i = 0; i < this.Promotion.length; i++) {
						let row = this.Promotion[i];
						if (row.countdown) {
							if (
								!(
									row.countdown.h == 0 &&
									row.countdown.m == 0 &&
									row.countdown.s == 0
								)
							) {
								if (row.countdown.s > 0) {
									row.countdown.s--;
									row.countdown.s =
										row.countdown.s < 10 ?
										'0' + row.countdown.s :
										row.countdown.s;
								} else if (row.countdown.m > 0) {
									row.countdown.m--;
									row.countdown.m =
										row.countdown.m < 10 ?
										'0' + row.countdown.m :
										row.countdown.m;
									row.countdown.s = 59;
								} else if (row.countdown.h > 0) {
									row.countdown.h--;
									row.countdown.h =
										row.countdown.h < 10 ?
										'0' + row.countdown.h :
										row.countdown.h;
									row.countdown.m = 59;
									row.countdown.s = 59;
								}
								this.Promotion[i].countdown = row.countdown;
							}
						}
					}
				}
			}, 1000);
		},
		//消息列表
		toMsg() {
			uni.navigateTo({
				url: '../msg/msg'
			})
		},
		//搜索跳转
		toSearch() {
			uni.showToast({
				title: '建议跳转到新页面做搜索功能'
			});
		},
		//轮播图跳转
		toSwiper(e) {
			uni.showToast({
				title: e.src,
				icon: 'none'
			});
		},
		//分类跳转
		toCategory(e) {
			//uni.showToast({title: e.name,icon:"none"});
			uni.navigateTo({
				url: '../goods/goods-list?cid=' + e.id + '&name=' + e.name
			});
		},
		//推荐商品跳转
		toPromotion(e) {
			uni.showToast({
				title: e.title,
				icon: 'none'
			});
		},
		//商品跳转
		toGoods(e) {
			uni.showToast({
				title: '商品' + e.goods_id,
				icon: 'none'
			});
			uni.navigateTo({
				url: '../goods/goods'
			});
		},
		//轮播图指示器
		swiperChange(event) {
			this.currentSwiper = event.detail.current;
		}
	}
};
